body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn-link {
    text-decoration: underline;
}

.btn-outline-primary {
    color: black;
    border-color: black;
    background: lightgray;
    border-radius: 0;
    width: 150px;
}

ul {
    padding-left: 0 !important;
}

ul li {
    list-style: none;
}

.img-holder {
    height: 600px;
}

.img-holder .img-holder-inner {
    display: block;
    width: auto;
    max-height: 100%;
}

.form-control {
    display: inline-block;
    max-width: 300px;
}

.category-frame {
    display: block;
    width: 100%;
}

.cat-container {
    max-width: 100%;
    margin: 0 auto;
}

.category-count {
    padding-left: 20px;
}

.catlist-img {
    max-width: 350px;
    height: 350px;
}

